.sk-wave[_ngcontent-c9] {
  position: relative;
  top: 50%;
  margin: auto;
  width: 50px;
  height: 40px;
  text-align: center;
  font-size: 10px;
}
.sk-wave[_ngcontent-c9] .sk-rect[_ngcontent-c9] {
  float: left;
  margin-right: 1px;
  height: 100%;
  width: 6px;
  display: inline-block;
  -webkit-animation: 1.2s ease-in-out infinite sk-waveStretchDelay;
  animation: 1.2s ease-in-out infinite sk-waveStretchDelay;
}
.sk-wave[_ngcontent-c9] .sk-rect1[_ngcontent-c9] {
  -webkit-animation-delay: -1.2s;
  animation-delay: -1.2s;
}
.sk-wave[_ngcontent-c9] .sk-rect2[_ngcontent-c9] {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.sk-wave[_ngcontent-c9] .sk-rect3[_ngcontent-c9] {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
.sk-wave[_ngcontent-c9] .sk-rect5[_ngcontent-c9] {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
.sk-wave[_ngcontent-c9] .sk-rect4[_ngcontent-c9] {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
@-webkit-keyframes sk-waveStretchDelay {
  0%,
  100%,
  40% {
    -webkit-transform: scaleY(0.4);
    transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
  }
}
@keyframes sk-waveStretchDelay {
  0%,
  100%,
  40% {
    -webkit-transform: scaleY(0.4);
    transform: scaleY(0.4);
  }
  20% {
    -webkit-transform: scaleY(1);
